import ChooseItemModel from "../games/model/ChooseItemModel";
import NumberInput from "./NumberInput";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BuyItemDelegate extends cc.Component {
    @property(cc.Label)
    gameNameLabel: cc.Label = null;

    @property(cc.Label)
    matchLabel: cc.Label = null;

    @property(cc.Label)
    teamLabel: cc.Label = null;

    @property(cc.Label)
    pointLabel: cc.Label = null;

    @property(cc.Label)
    minLabel: cc.Label = null;

    @property(cc.Label)
    maxLabel: cc.Label = null;

    @property(cc.Label)
    moneyLabel: cc.Label = null;


    @property(NumberInput)
    numberInput: NumberInput = null;


    private _item: ChooseItemModel = null;
    index: number = 0;
    target_key: string = "";

    private value: number = 0;
    target_item: string = "";


    get item(): ChooseItemModel {
        return this._item;
    }

    set item(value: ChooseItemModel) {
        this._item = value;
        this.updateData();
    }

    start() {
        cc.find("input_bg/input", this.node).on("onNumberChange", (event) => {
            console.log(event);
            let number = event.detail;
            this.onNumberChange(number);
        });
    }

    private updateData() {
        let name: string = this.item.name;
        name = name.replace('#team_home_name#', this.item.teams[0].name);
        name = name.replace('#team_guest_name#', this.item.teams[1].name);
        this.gameNameLabel.string = name;
        // this.matchLabel.string = this.item.match;


        if (this.item.teams.length > 0) {
            this.teamLabel.string = this.item.teams[0].name + "VS" + this.item.teams[1].name;
        } else {
            this.teamLabel.string = "";
        }
        if (this.item.type == 0) {
            this.matchLabel.string = "让球";
            this.target_key = this.index == 0 ? "home" : "guest";
            this.value = (this.index == 0 ? parseFloat(this.item.home) : parseFloat(this.item.guest));
            this.pointLabel.string = this.item.teams[this.index].name + " " + (this.index == 0 ? "让" : '受让') + this.item['type_name'] + "@" + (this.index == 0 ? this.item.home : this.item.guest);
        }
        if (this.item.type == 1) {
            this.matchLabel.string = "独赢";
            switch (this.index) {
                case 0:
                    this.target_key = "home";

                    this.value = parseFloat(this.item.home);
                    this.pointLabel.string = this.item.teams[0].name + "@" + parseFloat(this.item.home).toFixed(2);
                    break;
                case 1:
                    this.target_key = "same";
                    this.value = parseFloat(this.item.same);
                    this.pointLabel.string = "平@" + parseFloat(this.item.same).toFixed(2);
                    break;
                case 2:
                    this.target_key = "guest";
                    this.value = parseFloat(this.item.guest);
                    this.pointLabel.string = this.item.teams[1].name + "@" + parseFloat(this.item.guest).toFixed(2);
                    break;
            }
        }
        if (this.item.type == 2) {
            this.matchLabel.string = "大小";
            this.target_key = this.index == 0 ? "home" : "guest";
            this.value = (this.index == 0 ? parseFloat(this.item.home) : parseFloat(this.item.guest));
            this.pointLabel.string = this.index == 0 ? "大 " + this.item.under + "@" + parseFloat(this.item.home).toFixed(2) : "小 " + this.item.under + "@" + parseFloat(this.item.guest).toFixed(2);
        }
        if (this.item.type == 3) {
            this.matchLabel.string = "单双";
            this.target_key = this.index == 0 ? "sd_1" : "sd_2";
            this.value = (this.index == 0 ? parseFloat(this.item.sd_1) : parseFloat(this.item.sd_2));
            this.pointLabel.string = this.index == 0 ? ("单@" + this.item.sd_1) : "双@" + this.item.sd_2;
        }
        if (this.item.type == 4) {
            this.matchLabel.string = "波胆";
            this.target_key = this.item.target_key;
            this.target_item = this.item.target_item;
            this.pointLabel.string = this.item.label + "@" + parseFloat(this.item.value).toFixed(2);
            this.value = parseFloat(this.item.value);

            // this.pointLabel.string = "让" + this.item.handicap + "@" + (this.index == 0 ? this.item.home : this.item.guest);
        }
        if (this.item.type == 5) {
            this.matchLabel.string = "主队进球数";
            this.target_key = this.item.target_key;
            this.target_item = this.item.target_item;
            this.pointLabel.string = this.item.teams[0].name + " " + this.item.label+"球" + "@" + parseFloat(this.item.value).toFixed(2);
            this.value = parseFloat(this.item.value);
            // this.pointLabel.string = "让" + this.item.handicap + "@" + (this.index == 0 ? this.item.home : this.item.guest);
        }
        if (this.item.type == 6) {
            this.matchLabel.string = "客队进球数";
            this.target_key = this.item.target_key;
            this.target_item = this.item.target_item;
            this.pointLabel.string = this.item.teams[1].name + " " + this.item.label+"球" + "@" + parseFloat(this.item.value).toFixed(2);
            this.value = parseFloat(this.item.value);
            // this.pointLabel.string = "让" + this.item.handicap + "@" + (this.index == 0 ? this.item.home : this.item.guest);
        }
        if (this.item.type == 7) {
            this.matchLabel.string = "全场进球数";
            this.target_key = this.item.target_key;
            this.target_item = this.item.target_item;
            this.pointLabel.string = this.item.label + "球@" + parseFloat(this.item.value).toFixed(2);
            this.value = parseFloat(this.item.value);
            // this.pointLabel.string = "让" + this.item.handicap + "@" + (this.index == 0 ? this.item.home : this.item.guest);
        }
        // this.pointLabel.string =
        this.numberInput.placeHolder.string = "单注100-10000";
        this.minLabel.string = "最小投注:" + this.item.min_bet;
        this.maxLabel.string = "最大投注:" + this.item.max_bet;
    }


    private onNumberChange(number: any) {
        console.log(number);
        this.moneyLabel.string = (number * this.value).toFixed(2);
        // this.moneyLabel.string = parseFloat(number).toFixed(2) * this.item.
    }
}