import PageView = cc.PageView;

const {ccclass, property} = cc._decorator;

@ccclass
export default class AppPageView extends cc.ScrollView {

    @property({
        type: Number,
        slide: true,
        range: [0, 1, 0.01],
        tooltip: "滚动临界值"
    })
    scrollThreshold: number = 0.5;

    @property({
        type: Number

    })
    autoPageTurningThreshold: number = 100;

    _curPageIdx: number = 0;

    _lastPageIdx: number = 0;

    @property({
        type: cc.Float,
        tooltip: "页高度"
    })
    pageSizeHeight: number = 0;


    @property({
        type: Boolean
    })
    enableLoopAfter = false;

    private _pageSize: number = 4;


    prePage: cc.Node = null;

    currentPage: cc.Node = null;

    nextPage: cc.Node = null;


    static _cellPoolCache = [];


    @property({
        type: Number,
        tooltip: "每个页面翻页时所需时间。单位：秒"
    })
    pageTurningSpeed: number = 0.3;

    private _touchBeganPosition: cc.Vec2 = new cc.Vec2(0, 0);
    private _touchEndPosition: cc.Vec2 = new cc.Vec2(0, 0);


    // 是否超过自动滚动临界值
    _isScrollable(offset: cc.Vec2) {
        let offsetY = this.pageSizeHeight * this.scrollThreshold;
        // console.log(offsetY);
        return Math.abs(offset.y) >= offsetY;
    }

    _getDragDirection(moveOffset: cc.Vec2) {
        // 由于滚动 Y 轴的原点在在右上角所以应该是小于 0
        if (moveOffset.y === 0) {
            return 0;
        }
        return (moveOffset.y < 0 ? 1 : -1);
    }


    set pageSize(value: number) {
        this._pageSize = value;
        this.updateContentSize();
    }

    get pageSize(): number {
        return this._pageSize;
    }

    private updateContentSize() {
        this.content.height = this.pageSizeHeight * (this._pageSize + 1);
        this.scrollToPage(0,0);
    }


    _isQuicklyScrollable(touchMoveVelocity) {
        return Math.abs(touchMoveVelocity.y) > this.autoPageTurningThreshold;
    }

    _handleReleaseLogic(touch: cc.Event.EventTouch) {
        let bounceBackStarted = this._startBounceBackIfNeeded();
        let moveOffset = cc.pSub(this._touchBeganPosition, this._touchEndPosition);
        // console.log(pageSize);
        if (bounceBackStarted) {
            let dragDirection = this._getDragDirection(moveOffset);
            if (dragDirection === 0) {
                return;
            }
            if (dragDirection > 0) {
                this._curPageIdx = this._pageSize - 1;
            }
            else {
                this._curPageIdx = 0;
            }
        }
        else {
            let index = this._curPageIdx, nextIndex = index + this._getDragDirection(moveOffset);
            console.log(index, nextIndex);
            let timeInSecond = this.pageTurningSpeed * Math.abs(index - nextIndex);
            if (nextIndex < this._pageSize) {
                if (this._isScrollable(moveOffset)) {
                    this.scrollToPage(nextIndex, timeInSecond);
                    return;
                }
                else {
                    let touchMoveVelocity = this._calculateTouchMoveVelocity();
                    console.log(touchMoveVelocity);
                    if (this._isQuicklyScrollable(touchMoveVelocity)) {
                        this.scrollToPage(nextIndex, timeInSecond);
                        return;
                    }
                }
            }
            this.scrollToPage(index, timeInSecond);
        }
    }

    onLoad() {

    }

    setCurrentPage(index: number) {
        // this._curPageIdx = index;
        this.scrollToPage(index, 0);
    }

    // 通过 idx 获取偏移值数值
    _moveOffsetValue(idx) {
        let offset = cc.p(0, 0);
        offset.y = idx * this.pageSizeHeight;

        // console.log((offset));
        return offset;
    }


    /**
     * !#en Scroll PageView to index.
     * !#zh 滚动到指定页面
     * @method scrollToPage
     * @param {Number} idx index of page.
     * @param {Number} timeInSecond scrolling time
     */
    scrollToPage(idx, timeInSecond) {
        let page = this._pageSize;
        // console.log(page);
        if (idx < 0 || idx >= page)
            return;
        timeInSecond = timeInSecond !== undefined ? timeInSecond : 0.3;
        this._lastPageIdx = this._curPageIdx;
        this._curPageIdx = idx;
        this.scrollToOffset(this._moveOffsetValue(idx), timeInSecond, true);
        // setTimeout(() => {
        //     this.onPageChange();
        // }, timeInSecond * 1000)
        // this.scheduleOnce(this.onPageChange, timeInSecond);
    }

    _onTouchBegan(event: cc.Event.EventTouch, captureListeners: cc.EventListener) {
        this._touchBeganPosition = event.touch.getLocation();
        super._onTouchBegan(event, captureListeners)
    }

    _onTouchEnded(event: cc.Event.EventTouch, captureListeners: cc.EventListener) {
        this._touchEndPosition = event.touch.getLocation();
        super._onTouchEnded(event, captureListeners);
    }

    _onTouchCancelled(event: cc.Event.EventTouch, captureListeners: cc.EventListener) {
        this._touchEndPosition = event.touch.getLocation();
        super._onTouchCancelled(event, captureListeners);
    }


}




