import ChooseItemModel from "./model/ChooseItemModel";
import {default as AppData, GridType} from "../AppData";
import SectionGridItemDelegate from "./SectionGridItemDelegate";
import SectionGridItem from "./model/SectionGridItem";

const {ccclass, property} = cc._decorator;


@ccclass
export default class SectionRowDelegate extends cc.Component {

    @property(cc.Node)
    container: cc.Node = null;

    @property(cc.Node)
    sectionHeader: cc.Node = null;

    @property(cc.Label)
    sectionTitle: cc.Label = null;

    @property(cc.Prefab)
    gridItem: cc.Prefab = null;


    @property(cc.Sprite)
    icon: cc.Sprite = null;

    @property
    spaceX: number = 30;


    @property(SectionGridItemDelegate)
    sectionGridItems: SectionGridItemDelegate[] = [];

    private _type: number = 0;

    private _children: SectionGridItem[] = [];

    index: number = 0;

    private _model: SectionGridItem = null;


    get model(): SectionGridItem {
        return this._model;
    }

    set model(value: SectionGridItem) {
        this._model = value;
        if (this.model) {
            this.type = this.model.gridType;
            if (this.type == GridType.SECTION_TITLE) {
                this.sectionTitle.string = this.model.section.name;
            } else {
                this.children = this.model.children;
            }
        }
    }


    get children(): SectionGridItem[] {
        return this._children;
    }

    set children(value: SectionGridItem[]) {
        this._children = value;
        this.updateData();
    }

    private updateData() {
        for (let i = 0; i < this.sectionGridItems.length; i++) {
            let gridItemDelegate = this.sectionGridItems[i];
            if (i < this.children.length) {
                gridItemDelegate.node.active = true;
                let model = this.children[i];
                model.isLast = i == this.children.length - 1;
                gridItemDelegate.model = model;
            } else {
                gridItemDelegate.node.active = false;
            }
        }

    }

    @property({
        type: cc.Enum(GridType)
    })
    set type(value: number) {
        this._type = value;
        this.updateLayout();
    }

    private updateLayout() {
        switch (this.type) {
            case GridType.SECTION_TITLE:
                this.sectionHeader.active = true;
                this.container.active = false;
                break;
            case GridType.TYPE_DAXIAO:
                this.sectionHeader.active = false;
                this.container.active = true;
                this.updateType(3);
                break;
            case GridType.TYPE_DUYING:
                this.sectionHeader.active = false;
                this.container.active = true;
                this.updateType(3);

                break;
            case GridType.TYPE_RANG:
                this.sectionHeader.active = false;
                this.container.active = true;
                this.updateType(3);

                break;
            case GridType.TYPE_DANSHUANG:
                this.sectionHeader.active = false;
                this.container.active = true;
                this.updateType(3);

                break;
            case GridType.TYPE_BODAN:
            case GridType.TYPE_ZONG:
            case GridType.TYPE_KEDUI:
            case GridType.TYPE_ZHUDUI:
                this.sectionHeader.active = false;
                this.container.active = true;
                this.updateType(6);
                break;
        }

    }

    private updateType(number) {
        for (let i = 0; i < this.sectionGridItems.length; i++) {
            let gridItemDelegate = this.sectionGridItems[i];
            gridItemDelegate.gridType = this.type;
            gridItemDelegate.node.active = i < number;
            let x = 0.5 * gridItemDelegate.node.width + i * (gridItemDelegate.node.width + this.spaceX);
            gridItemDelegate.updateLayout(x, 0);
        }
    }


    get type(): number {
        return this._type;
    }

    updateItem(newIdx: number, x: number, y: number, newInfo: SectionGridItem) {
        this.index = newIdx;
        this.node.x = x;
        this.node.y = y;
        this.model = newInfo;
    }
}
