import ChooseItemModel from "./model/ChooseItemModel";
import BuyAlert from "../alert/BuyAlert";
import {buy} from "../utils/BuyUtils";
import SectionGridItemDelegate from "./SectionGridItemDelegate";
import SectionGridItem from "./model/SectionGridItem";

const {ccclass, property} = cc._decorator;

@ccclass
export default class ChooseItemType4Delegate extends SectionGridItemDelegate {
    private _item:ChooseItemModel = null;

    @property(cc.Label)
    homeLabel:cc.Label = null;

    @property(cc.Label)
    guestLabel:cc.Label = null;

    @property(cc.Label)
    pointLabel:cc.Label = null;

    @property(cc.Node)
    circle: cc.Node = null;


    get item(): ChooseItemModel {
        return this._item;
    }

    set item(value: ChooseItemModel) {
        this._item = value;
        this.updateData();
    }

    updateItem(x: number, y: number, number: number, model: SectionGridItem, reload: boolean) {
        // super.updateItem(x, y, number, model, reload);
        // if(model){
        //     this.item = model;
        // }
    }
    onItemClick(){
        console.log(this.item);
        BuyAlert.show("交易单",4,this.item,()=>{
            return buy();
        },0.3);
    }
    private updateData() {
        if(this.item){
            this.homeLabel.string = parseFloat(this.item.guest).toFixed(2) + "";
            this.guestLabel.string = parseFloat(this.item.home).toFixed(2) + "";
            this.pointLabel.string = this.item.over;
            if (this.pointLabel.string.length > 2) {
                this.circle.width = 96;
            } else if (this.pointLabel.string.length > 1) {
                this.circle.width = 80;
            } else {
                this.circle.width = 64;
            }
        }
        // let node = cc.find("label1")label1
    }
}