import ChooseItemModel from "../games/model/ChooseItemModel";
import BuyItemDelegate from "./BuyItemDelegate";
import easeBounceInOut = cc.easeBounceInOut;
import easeBounceIn = cc.easeBounceIn;
import easeBackOut = cc.easeBackOut;
import easeBackInOut = cc.easeBackInOut;
import easeBackIn = cc.easeBackIn;
import {hideKeyBoard, playAlertCloseSound, playAlertOpenSound, playBtnSound} from "../utils/Helper";
import {GridType} from "../AppData";
// import BuyItemDelegate from "./BuyItemDelegate";

const {ccclass, property} = cc._decorator;

@ccclass
export default class BuyAlert {

    private static _alert: cc.Node = null;

    private static _titleLabel: cc.Label = null;

    private static _cancelButton: cc.Node = null;

    private static _enterButton: cc.Node = null;

    public static _content: cc.Node = null;

    private static _speed: number = 0.3;

    private static actionFadeIn: cc.ActionInterval = null;

    private static _enterAction: Function = null;
    private static actionFadeOut: cc.ActionInterval = null;
    private static type: number = 0;
    private static _alertDialog: cc.Node = null;
    static _mask: cc.Node = null;
    static _keyboardContainer:cc.Node = null;

    static show(title: string, type: number, model: ChooseItemModel, enterAction: Function, speed: number) {
        if (this._alert != null) {

            return;
        }
        this._speed = speed ? speed : this._speed;
        this.type = type;
        cc.loader.loadRes("prefabs/AlertContent", cc.Prefab, (error, prefab) => {
            if (error) {
                cc.error(error);
                return;
            }
            BuyAlert._alert = cc.instantiate(prefab);
            let cbFadeOut = cc.callFunc(BuyAlert.onFadeOutFinish, this);
            let cbFadeIn = cc.callFunc(BuyAlert.onFadeInFinish, this);
            BuyAlert.actionFadeIn = cc.sequence(cc.spawn(cc.fadeIn(this._speed), cc.scaleTo(this._speed, 1.1).easing(easeBackOut())), cbFadeIn);
            BuyAlert.actionFadeOut = cc.sequence(cc.spawn(cc.fadeOut(this._speed), cc.scaleTo(this._speed, 0.5).easing(easeBackIn())), cbFadeOut);

            // 获取子节点
            BuyAlert._titleLabel = cc.find("alert/titleLabel", BuyAlert._alert).getComponent(cc.Label);
            BuyAlert._cancelButton = cc.find("alert/btnCancel", BuyAlert._alert);
            BuyAlert._enterButton = cc.find("alert/btnSubmit", BuyAlert._alert);
            BuyAlert._content = cc.find("alert/content", BuyAlert._alert);
            BuyAlert._alertDialog = cc.find('alert', BuyAlert._alert);
            BuyAlert._mask = cc.find('mask', BuyAlert._alert);
            // BuyAlert._mask.on("mousedown", (event) => {
            //     hideKeyBoard();
            // }, this);
            BuyAlert.setupType(model);
            this._keyboardContainer = cc.find("keyboardContainer",this._alert);
            BuyAlert._enterButton.on("click", BuyAlert.onButtonClicked, this);
            BuyAlert._cancelButton.on('click', BuyAlert.onButtonClicked, this);
            BuyAlert._alert.parent = cc.find("Canvas");
            BuyAlert.startFadeIn();

            BuyAlert.configAlert(title, enterAction, speed);

        });
    }

    static stopCloseAction() {
        if (BuyAlert._enterButton && BuyAlert._cancelButton) {
            BuyAlert._enterButton.pauseSystemEvents(true);
            BuyAlert._cancelButton.pauseSystemEvents(true);
        }
    }

    static resumeCloseAction() {
        if (BuyAlert._enterButton && BuyAlert._cancelButton) {
            BuyAlert._enterButton.resumeSystemEvents(true);
            BuyAlert._cancelButton.resumeSystemEvents(true);
        }
    }

    private static setupType(model: ChooseItemModel) {
        if (BuyAlert.type > 0) {
            switch (BuyAlert.type) {
                case GridType.TYPE_DUYING:
                    BuyAlert.setupContent(model, 3);
                    break;
                case GridType.TYPE_BODAN:
                case GridType.TYPE_ZHUDUI:
                case GridType.TYPE_KEDUI:
                case GridType.TYPE_ZONG:
                    BuyAlert.setupContent(model, 1);
                    break;
                case GridType.TYPE_RANG:
                    BuyAlert.setupContent(model, 2);
                    break;
                case GridType.TYPE_DAXIAO:
                    BuyAlert.setupContent(model, 2);
                    break;
                case GridType.TYPE_DANSHUANG:
                    BuyAlert.setupContent(model, 2);
                    break;
                default:
                    break;
            }
        }
    }

    // static tips:any[] =['让',]


    private static setupContent(model: ChooseItemModel, number: number) {
        cc.loader.loadRes("prefabs/buySelectItem", cc.Prefab, (error, prefab) => {
            if (error) {
                cc.error(error);
                return;
            }
            let node = cc.find("alert", this._alert);
            if (number == 2) {
                node.width = 984;
                BuyAlert._cancelButton.x = 448;
            } else if (number == 3) {
                node.width = 1428;
                BuyAlert._cancelButton.x = 672;

            } else if (number == 1) {
                node.width = 640;
                BuyAlert._cancelButton.x = 281;
            }
            BuyAlert._content.removeAllChildren();
            for (let i = 0; i < number; i++) {
                let buyItem = cc.instantiate(prefab);
                buyItem.parent = BuyAlert._content;
                if (number == 1) {
                    buyItem.width = 444;
                } else {
                    buyItem.width = 444;
                }
                let component = buyItem.getComponent(BuyItemDelegate);
                component.index = i;
                component.item = model;
            }
        })
    }

    // 按钮点击事件
    static onButtonClicked(event) {
        playBtnSound();
        if (event.target.name == "btnSubmit") {
            if (BuyAlert._enterAction) {
                let close = BuyAlert._enterAction();
                if (close) {
                    BuyAlert.startFadeOut();
                }
            }
        } else {
            BuyAlert.startFadeOut();
        }
    };

    static startFadeOut() {
        playAlertCloseSound();
        BuyAlert._alertDialog.pauseSystemEvents(true);
        BuyAlert._mask.runAction(cc.fadeOut(BuyAlert._speed));
        BuyAlert._alertDialog.runAction(BuyAlert.actionFadeOut);
    };


    // 参数
    static configAlert(title, enterCallBack, animSpeed) {

        // 回调
        BuyAlert._enterAction = enterCallBack;

        // 内容
        BuyAlert._titleLabel.string = title;

        BuyAlert._enterButton.x = 0;
        // }
    };

    // 弹进动画完成回调
    static onFadeInFinish() {

        BuyAlert._alertDialog.resumeSystemEvents(true);
    }

    // 弹出动画完成回调
    static onFadeOutFinish() {
        this.onDestory();
    };


    static onDestory() {
        BuyAlert._alert.destroy();
        BuyAlert._enterAction = null;
        BuyAlert._alert = null;
        BuyAlert._titleLabel = null;
        BuyAlert._cancelButton = null;
        BuyAlert._enterButton = null;
        BuyAlert._speed = 0.3;
    }


    static startFadeIn() {
        playAlertOpenSound();
        BuyAlert._alertDialog.pauseSystemEvents(true);
        BuyAlert._alertDialog.position = cc.p(0, 0);
        BuyAlert._alertDialog.setScale(0.5);
        BuyAlert._alertDialog.opacity = 0;
        BuyAlert._mask.opacity = 0;
        BuyAlert._mask.runAction(cc.fadeTo(BuyAlert._speed, 160));
        BuyAlert._alertDialog.runAction(BuyAlert.actionFadeIn);
    }

}

