

const {ccclass, property} = cc._decorator;

@ccclass
export default class MenuListScrollDelegate extends cc.Component {


    @property(cc.Sprite)
    arrowUp:cc.Sprite = null;

    @property(cc.Sprite)
    arrowDown:cc.Sprite = null;

    @property(cc.ScrollView)
    scrollView:cc.ScrollView = null;

    list:cc.Layout = null;

    onLoad(){
        this.arrowDown.node.on("click",()=>{
            this.scrollView.scrollToBottom(0.3,true);
        });
        this.arrowUp.node.on("click",()=>{
            this.scrollView.scrollToTop(0.3,true);
        });
        this.list = cc.find("view/menus",this.node).getComponent(cc.Layout);
    }


    onScroll(scrollView:cc.ScrollView,type){

    }

}