import SectionGridItemDelegate from "../games/SectionGridItemDelegate";
import SectionGridItem from "../games/model/SectionGridItem";
import SectionRowDelegate from "../games/SectionRowDelegate";
import nodeCaches from "../NodeCaches";
import {GridType} from "../AppData";

const {ccclass, property} = cc._decorator;

@ccclass
export default class RecycleView extends cc.Component {


    recycleItems: SectionGridItemDelegate[] = [];

    @property(cc.ScrollView)
    scrollView: cc.ScrollView = null;

    @property
    itemHeight: number = 72;

    @property(Number)
    spaceY: number = 30;

    @property
    layoutRow: number = 11;

    @property
    offsetStart: number = 0;


    scrollItems: SectionRowDelegate[] = [];

    private _dataList: SectionGridItem[] = [];
    private updateTimer: number = 0;
    private updateInterval: number = 0.2;
    private bufferZone: number = 350;
    private lastContentPosY: number = 0;


    get dataList(): SectionGridItem[] {
        return this._dataList;
    }

    set dataList(value: SectionGridItem[]) {
        this._dataList = value;
    }


    preItems() {
        // this.initCount =  this.itemPrefab.data.height
        let y = -this.itemHeight * 0.5;
        nodeCaches.moveItem = cc.instantiate(nodeCaches.prefabs.get("gameItem"));
        for (let i = 0; i < this.layoutRow; i++) {
            let node = cc.instantiate(nodeCaches.prefabs.get("sectionRow"));
            let row = node.getComponent(SectionRowDelegate);
            this.scrollItems.push(row);
            node.parent = this.node;
            row.updateItem(i, 0, y, null);
            y -= this.itemHeight + this.spaceY;
            node.active = false;
        }
    }

    scrollToPosition(index) {
        let data = this.dataList[index];

    }

    reloadData() {
        this.scrollView.scrollToTop(0);
        let y = -this.itemHeight * 0.5;

        for (let i = 0; i < this.scrollItems.length; i++) {
            let item = this.scrollItems[i];
            item.node.active = true;
            if (i < this.dataList.length) {
                let dataItem = this.dataList[i];
                item.updateItem(i, 0, y, dataItem);
                y -= this.itemHeight + this.spaceY;
            } else {
                item.node.active = false;
            }
        }
        this.setupScrollContent()
    }

    private setupScrollContent() {
        let row = 0;
        for (let i = this.dataList.length - 1; i > 0; i--) {
            let rowItem = this.dataList[i];
            row++;
            if (rowItem.gridType == 0) {
                break;
            }
        }
        let lastHeight = row * (this.itemHeight + this.spaceY);
        let pageHeight = this.scrollView.node.height;
        let fixed = 0;
        if (pageHeight > lastHeight) {
            fixed = pageHeight - lastHeight;
        } else {
            fixed = 0;
        }
        this.scrollView.content.height = (this.dataList.length) * (this.itemHeight + this.spaceY) + fixed;

    }


    getPositionInView(item) { // get item position in scrollview's node space
        let worldPos = item.parent.convertToWorldSpaceAR(item.position);
        let viewPos = this.scrollView.node.convertToNodeSpaceAR(worldPos);
        return viewPos;
    }

    update(dt) {
        this.updateTimer += dt;
        if (this.updateTimer < this.updateInterval) {
            return; // we don't need to do the math every frame
        }
        this.updateTimer = 0;
        let items = this.scrollItems;
        let buffer = this.bufferZone;
        let isDown = this.node.y < this.lastContentPosY; // scrolling direction
        let isUp = this.node.y > this.lastContentPosY;
        let curItemCount = this.scrollItems.length;
        let offset = (this.itemHeight + this.spaceY) * curItemCount;

        for (let i = 0; i < curItemCount; ++i) {
            let item = items[i];
            let itemNode = item.node;
            let viewPos = this.getPositionInView(itemNode);
            if (isDown) {
                // if away from buffer zone and not reaching top of content
                if (viewPos.y + this.offsetStart < -buffer && itemNode.y + offset < 0) {
                    let newIdx = item.index - curItemCount;
                    console.log(newIdx);
                    if (newIdx < this.dataList.length) {
                        let newInfo = this.dataList[newIdx];
                        item.updateItem(newIdx, 0, itemNode.y + offset, newInfo);

                    }
                }
            } else if (isUp) {
                // if away from buffer zone and not reaching bottom of content
                if (viewPos.y - this.offsetStart > buffer && itemNode.y - offset > -this.node.height) {
                    let newIdx = item.index + curItemCount;
                    // let newInfo = this.itemList[newIdx];
                    console.log(newIdx);
                    if (newIdx < this.dataList.length) {
                        let newInfo = this.dataList[newIdx];

                        item.updateItem(newIdx, 0, itemNode.y - offset, newInfo);

                    }
                }
            }
        }

        this.lastContentPosY = this.node.y;

    }


    start() {

    }

}