import BuyAlert from "../alert/BuyAlert";
import BuyItemDelegate from "../alert/BuyItemDelegate";
import NumberInput from "../alert/NumberInput";
import ApiNet from "./ApiNet";
import TipsAlert from "../alert/TipsAlert";

export function buy() {
    // let money =
    let data = [];
    let arena_id = "";
    let hasMin = false;
    if (BuyAlert._content) {
        BuyAlert._content.children.forEach((item) => {
            let numberInput = cc.find("input_bg/input", item).getComponent(NumberInput);
            let component = item.getComponent(BuyItemDelegate);
            let money = numberInput.number;
            if(parseInt(money)<100){
                hasMin = true;
                return;
            }
            arena_id = component.item.arena_id;
            console.log(money, component.item);
            if (money != "" && parseFloat(money) > 0) {
                let bet_item = {target: component.target_key, item:component.target_item, money: parseFloat(money).toFixed(2)};
                data.push(bet_item);
            }
        });
    }
    if(hasMin){
        TipsAlert.show(0.3, "错误", "金额未到达下注限额，请提高下注金额再试！", () => {

        });
        return false;
    }
    if (data.length > 0 ) {
        ApiNet.post("/index/arena/bets", {
            arena_id: arena_id,
            bet_data: Base64.encode(JSON.stringify(data))
        }).then((res) => {
            console.log(res);
            if (res.code == 0) {
                TipsAlert.show(0.3, "交易结果", "投注成功，请等比赛结束", () => {

                });
            } else {
                TipsAlert.show(0.3, "交易结果", "" + res.msg, null);
            }
        });
        return true;
    } else {
        TipsAlert.show(0.3, "错误", "请至少选择一项下注", () => {

        });
        return false;
    }

}

