import ApiNet from "../utils/ApiNet";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NoticeBar extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    is_runing: boolean = false;
    // LIFE-CYCLE CALLBACKS:
    message: string = '';

    onLoad() {
        this.label.string = ""
        this.runLoop();
    }

    runLoop() {

        let width = 700;
        this.label.string = this.message;
        this.label.node.x = 350;
        let time = ((this.label.node.width + width) / 30) * 0.3;
        let action = cc.moveBy(time, cc.p(-(this.label.node.width + width)));
        let finished = cc.callFunc(() => {
            //doSomething
            this.runLoop();
        }, this);
        this.label.node.runAction(cc.sequence(action, finished));
    }

    updateMessage() {
        ApiNet.post("/index/common/notice", {}).then((res) => {
            console.log(res);
            let data = res.data;
            let content = data.map((item) => {
                return item.content
            }).join(' ');
            this.message = content;
            console.log(this.label.node.width);
        }).catch((error) => {

        });
    }


    start() {
        this.schedule(this.updateMessage, 5, cc.macro.REPEAT_FOREVER, 0.3)
    }


    // update (dt) {}
}
