import TabLayout from "../widget/TabLayout";
import Tab from "../widget/Tab";
import BuyAlert from "./BuyAlert";
import {playAlertCloseSound, playAlertOpenSound} from "../utils/Helper";

const {ccclass, property} = cc._decorator;

@ccclass
export default class TipsAlert {

    private static _alert: cc.Node = null;

    private static _mask: cc.Node = null;

    private static _titleLabel: cc.Label = null;


    private static _enterButton: cc.Node = null;

    private static _speed: number = 0.3;

    private static actionFadeIn: cc.ActionInterval = null;

    private static _enterAction: Function = null;

    private static actionFadeOut: cc.ActionInterval = null;

    private static _bg: cc.Node = null;
    private static _cancelButton: cc.Node = null;


    static show(speed: number, title: string, content: string, enterAction: Function, submitString: string = '确定', cancelString: string = "取消", showCancel: boolean = false) {
        if (this._alert != null) {
            return;
        }
        this._speed = speed ? speed : this._speed;
        this._enterAction = enterAction;
        cc.loader.loadRes("prefabs/TipsAlert", cc.Prefab, (error, prefab) => {
            if (error) {
                cc.error(error);
                return;
            }
            this._alert = cc.instantiate(prefab);
            this._mask = cc.find("mask", this._alert);
            // this._bg = cc.find("alertBg", this._alert);
            // HelpAlert.updateData();
            let cbFadeOut = cc.callFunc(this.onFadeOutFinish, this);
            let cbFadeIn = cc.callFunc(this.onFadeInFinish, this);
            cc.find("alert/title", this._alert).getComponent(cc.Label).string = title;
            cc.find("alert/content", this._alert).getComponent(cc.Label).string = content;
            // this.actionFadeIn = cc.sequence(cc.moveBy(this._speed, cc.p(-this._bg.width)), cbFadeIn);
            // this.actionFadeOut = cc.sequence(cc.moveBy(this._speed, cc.p(this._bg.width)), cbFadeOut);
            // 获取子节点
            this._enterButton = cc.find("alert/btnSubmit", this._alert);
            this._cancelButton = cc.find("alert/btnCancel", this._alert);
            cc.find("label",this._enterButton).getComponent(cc.Label).string = submitString;
            cc.find("label",this._cancelButton).getComponent(cc.Label).string = cancelString;
            if(showCancel){
                this._enterButton.x = 160;
                this._cancelButton.x = -160;
                this._cancelButton.active = true;
            }else {
                this._enterButton.x = 0;
                this._cancelButton.x = 0;
                this._cancelButton.active = false;
            }

            this._enterButton.on("click", TipsAlert.onButtonClicked, this);
            this._cancelButton.on("click", TipsAlert.onButtonClicked, this);
            this.actionFadeIn = cc.sequence(cc.spawn(cc.fadeIn(this._speed), cc.scaleTo(this._speed, 1.1).easing(cc.easeBackOut())), cbFadeIn);
            this.actionFadeOut = cc.sequence(cc.spawn(cc.fadeOut(this._speed), cc.scaleTo(this._speed, 0.5).easing(cc.easeBackIn())), cbFadeOut);
            this._alert.parent = cc.find("Canvas");
            this.startFadeIn();

        });
    }

    // private static updateData() {
    //     let tabLayout = cc.find("alertBg/tabs", this._alert).getComponent(TabLayout);
    //     let helpTips = cc.find("alertBg/helpTips", this._alert).getComponent(TabLayout);
    //     let label = cc.find("alertBg/scrollView/view/list/label", this._alert).getComponent(cc.RichText);
    //     console.log(label);
    //
    //     // let article = this.helpData.typeData[0];
    //     label.string = article.content;
    //
    //     helpTips.node.on("onTabSelected", (event) => {
    //         console.log(event.detail);
    //     });
    //     tabLayout.node.on("onTabSelected", (event) => {
    //         console.log(event.detail);
    //     });
    //     cc.loader.loadRes("alert/tab", cc.Prefab, (error, prefab) => {
    //         if (error) {
    //             cc.error(error);
    //             return;
    //         }
    //         helpTips.clearTabs();
    //         for (let i = 0; i < this.helpData.typeData.length; i++) {
    //             let typeItem = this.helpData.typeData[i];
    //             let node = cc.instantiate(prefab);
    //             node.parent = helpTips.node;
    //             let component = node.getComponent(Tab);
    //             component.label.string = typeItem.name;
    //             console.log(component);
    //         }
    //         helpTips.setupTabs();
    //     });
    //
    // }

    // 按钮点击事件
    static onButtonClicked(event) {
        if (event.target.name == "btnSubmit") {
            if (this._enterAction) {
                this._enterAction();
            }
            TipsAlert.startFadeOut();
        }
        this.startFadeOut();
    };

    static startFadeOut() {
        playAlertCloseSound();
        cc.eventManager.pauseTarget(this._alert, true);
        this._mask.runAction(cc.fadeOut(this._speed));
        this._alert.runAction(this.actionFadeOut);
    };


    // 弹进动画完成回调
    static onFadeInFinish() {
        cc.eventManager.resumeTarget(this._alert, true);
    }

    // 弹出动画完成回调
    static onFadeOutFinish() {
        this.onDestory();
    };


    static onDestory() {
        this._alert.destroy();
        this._enterAction = null;
        this._alert = null;
        this._titleLabel = null;
        // this._cancelButton = null;
        this._enterButton = null;
        this._speed = 0.3;
    }


    static startFadeIn() {
        playAlertOpenSound();
        cc.eventManager.pauseTarget(this._alert, true);
        this._alert.position = cc.p(0, 0);
        this._mask.opacity = 0;
        this._mask.runAction(cc.fadeTo(this._speed, 156));
        this._alert.runAction(this.actionFadeIn);
    }

}

