import AppConfig from '../config'


function queryStringfy(obj) {
    let keys = Object.keys(obj);
    let str = '';
    keys.forEach(k => {
        if (obj[k]) {
            str += `&${k}=${obj[k]}`;
        }
    });

    return str.slice(1);
}

function loadRequest(url: string, data, method: string = "GET"): Promise<any> {
    var promise = new Promise<any>(function (resolve, reject) {
        let isreturn = false;
        setTimeout(() => {
            if (!isreturn) {
                reject(new Error("网络连接已超时"));
                isreturn = true;
            }
        }, 10000);
        let datastr = null;

        if (!url.startsWith("http://") && !url.startsWith("https://")) {
            url = AppConfig.HOST_URL + url;
        }

        if (url != AppConfig.HOST_URL + "/user/passport/login") {
            if (AppConfig.userData) {
                let {token} = AppConfig.userData;
                data.token = token;
            }
        }
        if (data) {
            datastr = queryStringfy(data);
        }
        var client = cc.loader.getXMLHttpRequest();//new XMLHttpRequest();
        if (method === "GET") {
            if (datastr) {
                client.open(method, url + "?" + datastr, true);
            } else {
                client.open(method, url, true);
            }

        } else if (method === "POST") {
            client.open(method, url, true);
            client.setRequestHeader("Content-type", "application/x-www-form-urlencoded");
        }
        //client.timeout = 5000;       //5秒后超时
        client.responseType = "text";

        client.onreadystatechange = (ev: Event) => {
            if (client.readyState < 4) return;
            if (isreturn) {
                return;
            }
            if (client.status === 200) {
                isreturn = true;
                // console.log("返回响应:" + client.response)
                resolve(JSON.parse(client.response));
            } else {
                isreturn = true;
                reject(new Error(client.statusText));
            }
        };

        client.onerror = (ev: Event) => {
            if (isreturn) {
                return;
            }
            isreturn = true;
            reject(new Error("网络请求出错"));
        }

        let log = `发送http请求 请求方式:${method} 请求地址:${url}`;
        if (datastr) {
            client.send(datastr);
            log += `?${datastr}`
        } else {
            client.send();
        }
        cc.log(log);

    });

    return promise;
}


export default {
    get: (url, data = {}) => {
        return loadRequest(url, data, "GET");
    },
    post: (url, data = {}) => {
        return loadRequest(url, data, "POST");
    }
}