import view = cc.view;

const {ccclass, property} = cc._decorator;

@ccclass
export default class ListItemDelegate extends cc.Component {
    index: number = 0;
    model: any = null;


    updateItem(index: number,x:number, y: number, model: any) {
        this.node.y = y;
        this.node.x = x;
        this.index = index;
        this.model = model;
    }

}