const {ccclass, property} = cc._decorator;

@ccclass
export default class LoadMore extends cc.Component {


    @property(cc.Node)
    loading:cc.Node = null;


    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    onScroll(scrollView:cc.ScrollView,event){
        let offsetY = Math.abs(scrollView.getMaxScrollOffset().y - scrollView.getScrollOffset().y);
        // if(event == cc.ScrollView.EventType.SCROLL_TO_BOTTOM){
        //     console.log("滚动到底部");
        // }
        if(event ==  cc.ScrollView.EventType.BOUNCE_BOTTOM && offsetY > 150){
            console.log('上拉加载')
            this.loading.runAction(cc.fadeIn(0.15));
            this.node.emit("LoadMore")
        }
    }



    start () {

    }

    // update (dt) {}
}
