const {ccclass, property} = cc._decorator;


@ccclass
export default class MenuScrollDelegate extends cc.Component {

    @property(cc.Node)
    arrowUp:cc.Node = null;

    @property(cc.Node)
    arrowDown:cc.Node = null;

    onScroll(scrollView: cc.ScrollView, event) {
        let number = Math.floor((scrollView.getScrollOffset().y));
        console.log(number);
        this.arrowUp.active = number > 0;
        this.arrowDown.active = number < Math.floor(Math.abs(scrollView.getMaxScrollOffset().y));

        // if (number == 0) {
        //
        //     // this.arrowDown.active = true;
        // }else if(number == Math.floor(Math.abs(scrollView.getMaxScrollOffset().y))) {
        //     this.arrowUp.active = true;
        //
        // }
    }
}