import AppGame from "../AppGame";
import AppData from "../AppData";
import nodeCaches from "../NodeCaches";
import MenuItemDelegate from "../menus/MenuItemDelegate";

const {ccclass, property} = cc._decorator;


@ccclass
export default class SectionScrollDelegate extends cc.Component {

    @property(cc.Node)
    content: cc.Node = null;

    isClick: boolean = false;


    @property(cc.Node)
    typeUpArrow: cc.Node = null;

    @property(cc.Node)
    typeDownArrow: cc.Node = null;


    updateContentLayout() {

    }

    onScroll(scrollView: cc.ScrollView, eventType: cc.ScrollView.EventType) {

        if (eventType == cc.ScrollView.EventType.SCROLL_ENDED || eventType == cc.ScrollView.EventType.SCROLLING) {
            this.updateMenuIndex(scrollView.getScrollOffset(), eventType, scrollView);
        }
        let number = Math.floor((scrollView.getScrollOffset().y));
        console.log(number);
        this.typeUpArrow.active = number > 0;
        this.typeDownArrow.active = number < Math.floor(Math.abs(scrollView.getMaxScrollOffset().y));

    }


    private updateMenuIndex(scrollOffset: cc.Vec2, eventType, scrollView: cc.ScrollView) {
        // console.log(scrollOffset);

        if (this.isClick) {
            console.log('---');
            if (eventType == cc.ScrollView.EventType.SCROLL_ENDED) {
                this.isClick = false;
            }
        } else {
            let offsetY = Math.floor(scrollOffset.y);
            // console.log(offsetY);
            if (offsetY < 0) {
                let component: MenuItemDelegate = AppData.showMenus[0];
                component.updateOther();
                component.item.selected = true;
                component.refresh();
                nodeCaches.lastSelectTypeMenu = component;
            } else if (offsetY >= scrollView.getMaxScrollOffset().y) {
                let component: MenuItemDelegate = AppData.showMenus[AppData.showMenus.length - 1];
                component.updateOther();
                component.item.selected = true;
                component.refresh();
                nodeCaches.lastSelectTypeMenu = component;
            } else {
                for (let i = 0; i < AppData.showMenus.length; i++) {
                    let component = AppData.showMenus[i];
                    if (offsetY >= -component.scrollStartY && offsetY < -component.scrollEndY) {
                        component.updateOther();
                        component.item.selected = true;
                        component.refresh();
                        nodeCaches.lastSelectTypeMenu = component;
                        break;
                    }else {

                    }

                }
            }
        }
    }
}