const {ccclass, property} = cc._decorator;


export enum StateType {
    EMPTY,
    LOADING,
    CONTENT
}

@ccclass
export default class StateView extends cc.Component {

    @property(cc.Node)
    loading: cc.Node = null;

    @property(cc.Sprite)
    emptyView: cc.Sprite = null;


    private _stateType: StateType = StateType.LOADING;


    get stateType(): StateType {
        return this._stateType;
    }

    @property
    set stateType(value: StateType) {
        this._stateType = value;
        this.updateContent();
    }


    start() {

    }

    // update (dt) {}
    private updateContent() {
        this.loading.active = false;
        this.emptyView.node.active = false;
        switch (this.stateType) {
            case StateType.LOADING:
                this.loading.active = true;
                this.emptyView.node.active = false;
                break;
            case StateType.EMPTY:
                this.loading.active = false;
                this.emptyView.node.active = true;
                break;
            case StateType.CONTENT:

                break;
        }
    }
}






