import Tab from "../widget/Tab";
import TabLayout from "../widget/TabLayout";
import appConfig from "../config";


const {ccclass, property} = cc._decorator;

@ccclass
export default class HelpTabContent extends cc.Component {


    private _tabIndex: number = 0;

    private _tabData: any[] = [];

    @property(cc.Node)
    tab1: cc.Node = null;

    @property(cc.Node)
    tab2: cc.Node = null;

    @property(cc.RichText)
    label1: cc.RichText = null;

    @property(cc.RichText)
    label2: cc.RichText = null;

    private _helpData = {typeData: [], commonData: []};


    get helpData(): { typeData: any[]; commonData: any[] } {
        return this._helpData;
    }

    set helpData(value: { typeData: any[]; commonData: any[] }) {
        this._helpData = value;
        this.updateTabs();
    }

    private updateTabs() {

        let tabLayout1 = cc.find("tips", this.tab1).getComponent(TabLayout);

        let tabLayout2 = cc.find("tips", this.tab2).getComponent(TabLayout);
        // let type_url = "http://localhost:8090/html";
        // this.webView1.url = type_url;
        tabLayout1.node.on("onTabSelected", (event) => {
            let index: number = event.detail;
            // this.webView1.evaluateJS("scrollTo(" + this.helpData.typeData[index].anchor + ")")
            this.label1.string = this.helpData.typeData[index].content;
        });

        cc.loader.loadRes("prefabs/tab", cc.Prefab, (error, prefab) => {
            if (error) {
                cc.error(error);
                return;
            }
            tabLayout1.clearTabs();
            tabLayout2.clearTabs();
            for (let i = 0; i < this._helpData.typeData.length; i++) {
                let typeItem = this._helpData.typeData[i];
                let node = cc.instantiate(prefab);
                node.parent = tabLayout1.node;
                let component = node.getComponent(Tab);
                component.label.string = typeItem.name;
                console.log(component);
                if(i == 0){
                    this.label1.string = typeItem.content;
                }
            }
            for (let i = 0; i < this._helpData.commonData.length; i++) {
                let typeItem = this._helpData.commonData[i];
                // let node = cc.instantiate(prefab);
                // node.parent = tabLayout2.node;
                // let component = node.getComponent(Tab);
                // component.label.string = typeItem.name;
                // console.log(component);
                if(i == 0){
                    this.label2.string = typeItem.content;
                }
            }
            tabLayout1.setupTabs();
            tabLayout2.setupTabs();
        });
        this.tabIndex = 0;
    }

    get tabIndex(): number {
        return this._tabIndex;
    }

    @property
    set tabIndex(value: number) {
        this._tabIndex = value;
        this.updateContent();
    }

    private updateContent() {
        if (this.tabIndex < this.node.childrenCount) {
            for (let i = 0; i < this.node.childrenCount; i++) {
                let node = this.node.children[i];
                node.active = i == this.tabIndex;
            }
        }
    }

// LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}
}
