import GameItemModel from "./model/GameItemModel";
import ListItemDelegate from "../widget/ListItemDelegate";
import {loadSpriteFrame, playSelectSound} from "../utils/Helper";
import TipsAlert from "../alert/TipsAlert";

const {ccclass, property} = cc._decorator;


@ccclass
export default class GameItemDelegate extends ListItemDelegate {

    private updateTimer: number = 0;
    private updateInterval: number = 1;

    get showTag(): boolean {
        return this._showTag;
    }

    @property
    set showTag(value: boolean) {
        this._showTag = value;
        if (this.showTag) {
            this.homeTag.runAction(cc.fadeIn(0.15));
            this.guestTag.runAction(cc.fadeIn(0.15));
        } else {
            this.homeTag.runAction(cc.fadeOut(0.15));
            this.guestTag.runAction(cc.fadeOut(0.15));
        }
    }

    @property(cc.Node)
    homeTag: cc.Node = null;


    @property(cc.Node)
    guestTag: cc.Node = null;

    @property(cc.Label)
    leftLabel: cc.Label = null;

    @property(cc.Label)
    rightLabel: cc.Label = null;


    @property(cc.Label)
    nameLabel: cc.Label = null;

    @property(cc.Label)
    numberLabel: cc.Label = null;

    @property(cc.Sprite)
    leftIcon: cc.Sprite = null;

    @property(cc.Sprite)
    rightIcon: cc.Sprite = null;

    @property(cc.Label)
    timeLabel: cc.Label = null;


    enable: boolean = true;


    private _item: GameItemModel = null;


    private _showTag: boolean = false;


    init(index, data, reload, group) {
        console.log(index);
        this.index = index;
        if (reload) {
            if (index < data.array.length) {
                this.node.opacity = 255;
                this.item = data.array[index];

            } else {
                this.node.pauseSystemEvents(true);
                this.node.opacity = 0;
            }
        }

    }

    get item(): GameItemModel {
        return this._item;
    }

    set item(value: GameItemModel) {
        this._item = value
        this.updateData();
    }


    updateItem(index: number, x: number, y: number, model: any): void {
        super.updateItem(index, x, y, model);
        this.item = model;
    }

    private updateData() {
        // let map= new Map<string, string>();
        if (this.item) {
            for (let i = 0; i < this.item.teams.length; i++) {
                let item = this.item.teams[i];
                if (item.has_home == 1) {
                    this.leftLabel.string = item.name;
                    if (this.leftLabel.string.length > 6 && this.leftLabel.string.length <= 8) {
                        this.leftLabel.fontSize = 32;
                    } else if (this.leftLabel.string.length > 8) {
                        this.leftLabel.fontSize = 28;
                    } else {
                        this.leftLabel.fontSize = 42;
                    }
                    // displayImage.load(item.logo).to({url:item.logo,sprite:this.leftIcon});
                    loadSpriteFrame(item.logo).then((res: { img: cc.SpriteFrame, url: string }) => {
                        this.leftIcon.spriteFrame = res.img;
                    })
                    // }
                } else {
                    this.rightLabel.string = item.name;
                    if (this.rightLabel.string.length > 6 && this.rightLabel.string.length <= 8) {
                        this.rightLabel.fontSize = 32;
                    } else if (this.rightLabel.string.length > 8) {
                        this.rightLabel.fontSize = 28;
                    } else {
                        this.rightLabel.fontSize = 42;
                    }
                    loadSpriteFrame(item.logo).then((res: { img: cc.SpriteFrame, url: string }) => {
                        this.rightIcon.spriteFrame = res.img;
                    })
                }
            }
            // console.log(this.item);
            if (this.item.match) {
                this.nameLabel.string = this.item.match
            }
            if (this.item.play_time > 0) {
                let dayjs = window['dayjs'];
                let date = dayjs(this.item.play_time * 1000);
                let late = dayjs().isAfter(date);
                // if(now.)
                if (late) {
                    this.numberLabel.string = "比赛已开始";
                } else {
                    this.numberLabel.string = date.format("MM月DD日 HH:mm");
                }
            }
        }

    }


    update(dt) {
        this.updateTimer += dt;
        if (this.updateTimer < this.updateInterval) {
            return; // we don't need to do the math every frame
        }
        this.updateTimer = 0;
        // console.log(dt);
        if (this.item && parseInt(this.item.status) == 1) {
            let diff = this.item.play_time * 1000 - new Date().getTime();
            if(diff > 0){
                let day = this.formatTime(diff);
                this.timeLabel.string = day;
            }else {
                this.timeLabel.string = "停止投注";
            }

        } else {
            this.timeLabel.string = "停止投注"
        }

    }

    formatTime(number: number) {
        let second = Math.floor(number / 1000);
        let day = Math.floor(second / (3600 * 24));
        let temp = second % (3600 * 24);
        let hour = Math.floor(temp / (3600));
        temp = temp % (3600);
        let min = Math.floor(temp / 60);
        temp = temp % 60;
        let sec = Math.floor(temp);
        // console.log(day, hour,min, sec);
        let str = day + "天 ";
        if (hour < 10) {
            str += "0" + hour
        }else {
            str += hour;
        }
        str += ":";
        if (min < 10) {
            str += "0" + min
        }else {
            str += min;
        }
        str += ":";
        if (sec < 10) {
            str += "0" + sec
        }else {
            str += sec;
        }
        return str;
    }


    onClickItem() {
        if (this.node.opacity == 0) {
            return;
        }
        if(parseInt(this.item.status) > 1){
            TipsAlert.show(0.3,"提示",'本场比赛已经停止投注，感谢您的支持！',()=>{

            });
            return;
        }
        // console.log(this.item);
        let x = this.item.index % 2 == 0 ? (-274) : 540;
        let row = Math.floor(this.item.index / 2) % 5;
        let y = (-row + 2) * 140 + 20;
        // console.log(x, y);
        if (this.item.action) {
            playSelectSound();
            console.log('-----');
            this.item.action(x, y, this);
        }
    }


}
