import AppData from "../AppData";
import {playBackgroundSound, playBtnSound, playBtnSoundAway} from "../utils/Helper";

const {ccclass, property} = cc._decorator;

@ccclass
export default class AudioButton extends cc.Component {

    @property(cc.Sprite)
    enableSprite: cc.Sprite = null;


    @property(cc.Sprite)
    enableDisabled: cc.Sprite = null;


    private _isEnableSound: boolean = true;




    get isEnableSound(): boolean {
        return this._isEnableSound;
    }

    @property
    set isEnableSound(value: boolean) {
        this._isEnableSound = value;
        this.updateAudio();
    }

    private updateAudio() {
        this.enableSprite.node.active = this.isEnableSound
        this.enableDisabled.node.active = !this.isEnableSound
        this.toggleSound()
    }

    private toggleSound() {
        cc.sys.localStorage.setItem("sound", this.isEnableSound ? "1" : "0");
        if (!this.isEnableSound) {
            cc.audioEngine.stopAll();
        }
        AppData.enableSound = this.isEnableSound;
    }

    onLoad() {
        let sound = cc.sys.localStorage.getItem("sound");
        if (sound) {
            this.isEnableSound = parseInt(sound) == 1;
        }
        AppData.enableSound = this.isEnableSound;

    }

// LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

        this.node.on("click", () => {
            playBtnSoundAway();
            this.isEnableSound = !this.isEnableSound;
            playBackgroundSound();
        })
    }

    // update (dt) {}
}
