import {loadSpriteFrame, playMenuSelectSound, playSelectSound} from "../utils/Helper";
import nodeCaches from "../NodeCaches";
import AppData from "../AppData";
import ListView from "../widget/ListView";
import SectionRowDelegate from "../games/SectionRowDelegate";

const {ccclass, property} = cc._decorator;

@ccclass
export default class MenuItemDelegate extends cc.Component {

    @property(cc.Label)
    label: cc.Label = null;

    @property(cc.Sprite)
    background: cc.Sprite = null;

    @property(cc.Sprite)
    icon: cc.Sprite = null;

    private _item: any = null;

    scrollStartY = 0;
    scrollEndY = 0;

    @property(cc.SpriteFrame)
    selectedBackgroundFrame: cc.SpriteFrame = null;

    @property(cc.SpriteFrame)
    backgroundFrame: cc.SpriteFrame = null;

    iconSelectedFrame: cc.SpriteFrame = null;

    iconFrame: cc.SpriteFrame = null;

    type: string = "menu";


    // updateItem(index: number, y: number, model: any): void {
    //     super.updateItem(index, y, model);
    //     if(model){
    //         this.item = model;
    //     }
    // }

    get item(): any {
        return this._item;
    }

    set item(value: any) {
        this._item = value;
        // if(this.iconFrame && this.)
        this.loadFrames();

    }

    onMenuItemClick() {

        if (!this.item.selected) {
            this.updateOther();
            this.item.selected = true;
            if(this.type == "menu"){
                nodeCaches.lastMatchMenu = this;
            }else {
                nodeCaches.lastSelectTypeMenu = this;
            }
            this.refresh();
        }
        playMenuSelectSound();
        console.log(this.item);
        if (this.item.action) {
            this.item.action(this.item,this);
        }
    }

    updateOther() {
        if(this.type == "menu"){
            if(nodeCaches.lastMatchMenu){
                let component:MenuItemDelegate =  nodeCaches.lastMatchMenu;
                component.item.selected = false;
                component.refresh();
            }

        }else {
            if(nodeCaches.lastSelectTypeMenu){
                let component:MenuItemDelegate =  nodeCaches.lastSelectTypeMenu;
                component.item.selected = false;
                component.refresh();
            }
        }
    }

    private loadFrames() {
        this.label.string = this.item.name;
        Promise.all([loadSpriteFrame("menus/" + this.item.logo), loadSpriteFrame("menus/" + this.item.logo_hover)]).then((items) => {
            this.iconFrame = <cc.SpriteFrame>items[0]['img'];
            this.iconSelectedFrame = <cc.SpriteFrame>items[1]['img'];
            this.refresh();
        }).catch((error) => {
            console.log(error);
            this.refresh();
        })
    }

    refresh() {
        this.background.spriteFrame = this.item.selected ? this.selectedBackgroundFrame : this.backgroundFrame;
        this.icon.spriteFrame = this.item.selected ? this.iconSelectedFrame : this.iconFrame;
    }
}