import ApiNet from "./utils/ApiNet";
import appConfig from './config';
import {exitGame, loadGameData} from "./utils/Helper";
import MenuItemDelegate from "./menus/MenuItemDelegate";
import AppData from "./AppData";
import nodeCaches from "./NodeCaches";
import RecycleView from "./widget/RecycleView";
import TipsAlert from "./alert/TipsAlert";

const {ccclass, property} = cc._decorator;
// import HotUpdate from './utils/HotUpdate';


@ccclass
export default class Loading extends cc.Component {

    // LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    @property(cc.ProgressBar)
    progressBar: cc.ProgressBar = null;

    @property(cc.Node)
    football: cc.Node = null;

    @property(RecycleView)
    oddsList: RecycleView = null;


    @property(cc.AudioClip)
    bgAudio: cc.AudioClip = null;

    @property(cc.AudioClip)
    btnAudio: cc.AudioClip = null;

    @property(cc.AudioClip)
    alertOpenAudio: cc.AudioClip = null;

    @property(cc.AudioClip)
    menuSelectAudio: cc.AudioClip = null;


    @property(cc.AudioClip)
    alertCloseAudio: cc.AudioClip = null;

    @property(cc.AudioClip)
    selectedAudio: cc.AudioClip = null;

    loading: boolean = true;
    private hotUpdate: HotUpdate;
    private checkUpdated: boolean = false;

    start() {

        if (cc.sys.isNative) {
            if (cc.sys.OS_ANDROID === cc.sys.os) {
                appConfig.appGameInfo = null;
                appConfig.appUserInfo = null;
                appConfig.userData = null;
            } else {
                appConfig.HOST_URL = appConfig.appGameInfo.gameHost;
                appConfig.IMG_URL = "";
            }
            this.hotUpdate = cc.find("update").getComponent("HotUpdate");
            this.hotUpdate.checkUpdate();
            this.hotUpdate.callback = (needUpdate) => {
                this.updateCheck(needUpdate);
            }
            this.hotUpdate.progressCallback = (progress) => {
                this.progressBar.progress = progress;
                this.football.x = 1048 * this.progressBar.progress;
            }
        } else {
            this.initLoad();
        }
    }

    initLoad() {
        this.loadPrefabs();
        cc.director.preloadScene("main", ((error) => {
            this.waiting();
        }));
    }

    updateCheck(needUpdate) {
        cc.log("是否需要更新----" + (needUpdate ? "是" : "否"));
        if (needUpdate) {
            TipsAlert.show(0.3, '提示', "检测到新版本，需要更新", () => {
                this.hotUpdate.hotUpdate();
            });
        } else {
            this.initLoad();
        }
    }

    waiting() {
        if (appConfig) {
            let config = appConfig;
            if (cc.sys.OS_ANDROID === cc.sys.os) {

                try {
                    this.setupConfig(config);
                } catch (e) {
                    TipsAlert.show(0.3, '错误', "获取平台信息失败。", () => {
                        exitGame();
                    }, "确定")
                }

            }
            if (config.appUserInfo == null && config.appGameInfo == null && this.loading) {
                this.scheduleOnce(() => {
                    this.waiting();
                }, 0.3)
            } else {
                this.requestLogin();
            }
        }
    }

    /**
     * 设置初始化参数
     * @param config
     */
    private setupConfig(config) {
        let gameInfo = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getGameInfo", "()Ljava/lang/String;");
        let userInfo = jsb.reflection.callStaticMethod("org/cocos2dx/javascript/AppActivity", "getUserInfo", "()Ljava/lang/String;");
        config.appUserInfo = JSON.parse(userInfo);
        config.appGameInfo = JSON.parse(gameInfo);
        config.appGameInfo.gameHost = config.appGameInfo.gameHost.replace("\\/g", "");
        config.HOST_URL = config.appGameInfo.gameHost;
        config.IMG_URL = "";
    }

    private requestLogin() {
        ApiNet.post("/user/passport/login", {
            uuid: appConfig.appUserInfo.uuid,
            userName: appConfig.appUserInfo.userName,
            phone: appConfig.appUserInfo.phone,
            cpid: appConfig.appUserInfo.cpid,
            location: appConfig.appUserInfo.location,
            sexMode: appConfig.appUserInfo.sexModel,
            imei: appConfig.appUserInfo.imei,
            gamekey: appConfig.appGameInfo.gameKey,
            token: appConfig.appUserInfo.token
        }).then((res) => {
            console.log(res);
            if (res && res.code == 0) {
                appConfig.userData = res.data;
                loadGameData().then((res) => {
                    this.setupAppMenus();
                    cc.director.loadScene("main")
                })
            } else {
                TipsAlert.show(0.3, '错误', "链接服务器失败，请稍后再试!", () => {
                    exitGame();
                }, "确定")
            }
        }).catch((error) => {
            console.log(error);
            TipsAlert.show(0.3, '错误', "链接服务器失败，请稍后再试!", () => {
                exitGame();
            }, "确定")
        })
    }


    private loadPrefabs() {
        // cc.loader.loadResDir("prefabs")
        cc.loader.loadResDir("prefabs", cc.Asset, (count, total, item) => {
            let progress = 0.8 * (count / total);
            if (this.progressBar) {
                this.progressBar.progress = progress;
                this.football.x = 1048 * progress;
            }
        }, (error, asset) => {
            if (error) {
                console.log(error);

            } else {
                console.log(asset);
                asset.forEach(item => {
                    nodeCaches.prefabs.set(item._name, item);
                });
                console.log(nodeCaches.prefabs);
                this.loadAudio();
            }
        })

    }

    loadAudio() {
        AppData.sound.bg = this.bgAudio;
        AppData.sound.open = this.alertOpenAudio;
        AppData.sound.close = this.alertCloseAudio;
        AppData.sound.btn = this.btnAudio;
        AppData.sound.select = this.selectedAudio;
        AppData.sound.menu = this.menuSelectAudio;

        this.preItems();

    }


    private setupMatches(mathRes: any) {

    }

    // update (dt) {}
    private setupGames(mathRes: any) {

    }

    private setupAppMenus() {
        for (let i = 0; i < AppData.matches.length; i++) {
            let menuItem: cc.Node = null;
            if (nodeCaches.menuItemCaches.size() > 0) {
                menuItem = nodeCaches.menuItemCaches.get();
            } else {
                menuItem = cc.instantiate(nodeCaches.prefabs.get("menuItem"));
            }
            let menuModel = AppData.matches[i];
            let component = menuItem.getComponent(MenuItemDelegate);
            component.item = menuModel;
            component.type = "menu";
            nodeCaches.menusItemCaches.push(menuItem)
            if (this.progressBar) {
                this.progressBar.progress = 0.8 + (0.2 * (i + 1) / AppData.matches.length);
                this.football.x = this.progressBar.progress * 1048;
            }
        }
    }


    /**
     * 预加载菜单
     */
    private preItems() {
        this.oddsList.preItems();
        nodeCaches.sectionLists.put(this.oddsList.scrollView.node);

        for (let i = 0; i < AppData.rules.length; i++) {
            let menuItem: cc.Node = null;
            if (nodeCaches.menuItemCaches.size() > 0) {
                menuItem = nodeCaches.menuItemCaches.get();
            } else {
                menuItem = cc.instantiate(nodeCaches.prefabs.get("menuItem"));
            }
            let component = menuItem.getComponent(MenuItemDelegate);
            component.item = AppData.rules[i];
            component.type = "type";
            nodeCaches.typeMenuItemCaches.push(menuItem);
        }

        this.loading = false;
    }
}
