import ChooseItemModel from "./model/ChooseItemModel";
import BuyAlert from "../alert/BuyAlert";
import ChooseItemType1Delegate from "./ChooseItemType1Delegate";
import ChooseItemType2Delegate from "./ChooseItemType2Delegate";
import ChooseItemType5Delegate from "./ChooseItemType5Delegate";
import ChooseItemType4Delegate from "./ChooseItemType4Delegate";
import ChooseItemType3Delegate from "./ChooseItemType3Delegate";
import SectionGridItemDelegate from "./SectionGridItemDelegate";

const {ccclass, property} = cc._decorator;


@ccclass
export default class SectionItemDelegate extends cc.Component {

    @property(cc.Node)
    container: cc.Node = null;

    @property(cc.Sprite)
    sectionHeader: cc.Sprite = null;

    @property(cc.Label)
    sectionTitle: cc.Label = null;

    @property(cc.Prefab)
    sectionGridPrefab: cc.Prefab = null;


    @property(cc.Sprite)
    icon: cc.Sprite = null;

    @property(ChooseItemModel)
    items: ChooseItemModel[] = [];

    listItems: SectionGridItemDelegate[] = [];

    index: number = 0;

    @property(Number)
    spaceX: number = 0;

    @property(Number)
    spaceY: number = 0;

    preItemCount = 10;

    _type: number = 0;

    col: number = 0;

    row: number = 0;

    lastRow: number = 0;

    private itemWidth: number = 0;
    private itemHeight: number = 0;


    public set type(v: number) {
        this._type = v;
        this.updatePreItem()
    }

    public get type(): number {
        return this._type;
    }


    onLoad() {

    }

    updatePreItem() {
        switch (this.type) {
            case 0:
                this.preItemCount = 20;
                break;
            case 1:
                this.preItemCount = 40;
                break;
            case 2:
                this.preItemCount = 10;
                break;
            case 3:
                this.preItemCount = 5;
                break;
            case 4:
                this.preItemCount = 5;
                break;
            default:
                break;
        }
    }

    preItems() {
        this.itemWidth = this.sectionGridPrefab.data.width;
        this.itemHeight = this.sectionGridPrefab.data.height;
        this.col = Math.floor((this.container.width + this.spaceX) / (this.itemWidth + this.spaceX));
        // console.log(this.col);
        this.row = Math.ceil(this.preItemCount / this.col);
        // console.log(this.row)
        for (let i = 0; i < this.preItemCount; i++) {
            let itemPrefab = cc.instantiate(this.sectionGridPrefab);
            this.container.addChild(itemPrefab);
            let sectionGridItemDelegate = itemPrefab.getComponent(SectionGridItemDelegate);
            this.listItems.push(sectionGridItemDelegate);
            sectionGridItemDelegate.node.active = false;
        }
        this.node.active = false;
        this.lastRow = this.row;
    }

    reloadData() {
        this.node.active = true;
        // console.log(new Date().getTime());
        this.row = Math.ceil(this.items.length / this.col);

        if (this.listItems.length > this.items.length) {
            for (let i = 0; i < this.listItems.length; i++) {
                let gridItemDelegate = this.listItems[i];
                if (i < this.items.length) {
                    let item = this.items[i];
                    gridItemDelegate.node.active = true;
                    let itemRow = Math.floor(i / this.col);
                    let itemCol = Math.floor(i % this.col);
                    let vec = this.getLayoutVec(itemRow, itemCol);

                    gridItemDelegate.updateItem(vec.x, vec.y, item, true);

                } else {
                    gridItemDelegate.node.active = false;
                }
            }
        } else {
            for (let i = 0; i < this.items.length; i++) {

                let item = this.items[i];
                let itemRow = Math.floor(i / this.col);
                let itemCol = Math.floor(i % this.col);
                if (i < this.listItems.length) {
                    let gridItemDelegate = this.listItems[i];
                    gridItemDelegate.node.active = true;
                    let vec = this.getLayoutVec(itemRow, itemCol);
                    gridItemDelegate.updateItem(vec.x, vec.y, item, true);
                } else {
                    let itemPrefab = cc.instantiate(this.sectionGridPrefab);
                    this.container.addChild(itemPrefab);
                    // this.container.addChild(this.itemPrefab);
                    let sectionGridItemDelegate = itemPrefab.getComponent(SectionGridItemDelegate);
                    this.listItems.push(sectionGridItemDelegate);
                    let itemRow = Math.floor(i / this.col);
                    let itemCol = Math.floor(i % this.col);
                    let vec = this.getLayoutVec(itemRow, itemCol);
                    sectionGridItemDelegate.updateItem(vec.x, vec.y, item, true);
                }
            }
        }

        console.log("row -----" + this.row);
        this.container.height = this.row * this.itemHeight + (this.row - 1) * this.spaceY;
        this.node.height =  this.row * this.itemHeight + (this.row - 1) * this.spaceY  + 80 + 60;
        console.log(this.node.height);
        // console.log(new Date().getTime());

    }

    private getLayoutVec(row: number, col: number) {

        let x = 0.5 * this.itemWidth + col * (this.itemWidth + this.spaceX);
        let y = -row * (this.itemHeight + this.spaceY) - 0.5 * this.itemHeight;
        console.log(x,y);
        return {x: x, y: y}
    }


    private onSectionItemClick(i: number, model: ChooseItemModel) {
        if (this.type > 0) {
            console.log('-----');
            BuyAlert.show("交易单", this.type, model, () => {

            }, 0.3);
        }
    }

    private setupData(node: cc.Node, model: ChooseItemModel) {
        let typeDelegate: any = null;
        switch (this.type) {
            case 1:
                typeDelegate = node.getComponent(ChooseItemType1Delegate);
                break;
            case 2:
                typeDelegate = node.getComponent(ChooseItemType2Delegate);
                break;
            case 3:
                typeDelegate = node.getComponent(ChooseItemType3Delegate);

                break;
            case 4:
                typeDelegate = node.getComponent(ChooseItemType4Delegate);
                break;
            case 5:
                typeDelegate = node.getComponent(ChooseItemType5Delegate);
                break;
        }
        if (typeDelegate) {
            typeDelegate.item = model;
        }
    }
}
