import TeamModel from "./TeamModel";

const {ccclass, property} = cc._decorator;
@ccclass
export default class GameItemModel {
    index:number = 0;
    action:Function = null;
    x: number;
    y: number;
    match:string = "";
    teams:TeamModel[] = [];
    play_time: number = 0;
    play_id:number = 0;
    id:number = 0;
    status: string;

}