import ListItemDelegate from "../widget/ListItemDelegate";
import AppGame from "../AppGame";
import {playSelectSound} from "../utils/Helper";
import SearchListAlert from "./SearchListAlert";

const {ccclass, property} = cc._decorator;

@ccclass
export default class SearchItemDelegate extends ListItemDelegate {

    @property(cc.Label)
    timeLabel: cc.Label = null;
    @property(cc.Label)
    infoLabel: cc.Label = null;
    @property(cc.Label)
    resultLabel: cc.Label = null;

    private _item: any = null;


    get item(): any {
        return this._item;
    }

    set item(value: any) {
        this._item = value;
        this.updateData()
    }


    updateItem(index: number, x: number, y: number, model: any): void {
        super.updateItem(index, x, y, model);
        if(model){
            this.item = model;
        }
    }

    onItemClick(){
        console.log(this.item);
        if (parseInt(this.item.status) == 1) {
            playSelectSound();
            this.node.emit('onItemSelect', this.index);
            cc.find("AppGame").getComponent(AppGame).onLogSelect(this.item);
            SearchListAlert.startFadeOut();
        }
    }

    private updateData() {
        let dayjs = window['dayjs'];

        if (this.item) {
            let date = dayjs(this.item.play_time * 1000);
            this.timeLabel.string = date.format("MM月DD日\nHH:mm");
            // this.timeLabel.string = time.toLocaleDateString();
            this.infoLabel.string = this.item.match.name + "\n" + this.item.teams[0].name + "VS" + this.item.teams[1].name;
            let status = this.item.status;
            if (status == 1) {
                this.resultLabel.string = "比赛未开始";
                this.resultLabel.node.color = cc.hexToColor("#f0a51e")
            } else if (status == 2) {
                this.resultLabel.string = "比赛进行中";
                this.resultLabel.node.color = cc.hexToColor("#45f02b")
            } else {
                this.resultLabel.string = "比赛结果\n" + this.item.teams[0].score + ":" + this.item.teams[1].score;
                this.resultLabel.node.color = cc.hexToColor("#fff4f0")
            }

        }
    }

    // update (dt) {}
}
