import Tab from "./Tab";
import {playSelectSound} from "../utils/Helper";

const {ccclass, property} = cc._decorator;

@ccclass
export default class TabLayout extends cc.Component {

    selectedTab: Tab = null;

    onLoad() {
        this.setupTabs();
    }


    clearTabs(){
        this.node.removeAllChildren();
    }

    setupTabs() {

        for (let i = 0; i < this.node.childrenCount; i++) {
            let child = this.node.children[i];
            let component = child.getComponent(Tab);
            console.log(component);
            if (component) {
                child.on("click", () => {
                    this.onTabSelected(component, i);
                });
                component.selected = false;
                if (i == 0) {
                    component.selected = true;
                    this.selectedTab = component;
                }
            }
        }
    }

    private onTabSelected(component: Tab, index: number) {
        console.log(index);
        playSelectSound();
        if (component != this.selectedTab) {
            this.selectedTab.selected = false;
            component.selected = true;
            this.selectedTab = component;
            this.node.emit("onTabSelected", index);
        }
    }
}