import view = cc.view;
import ListItemDelegate from "./ListItemDelegate";
import AppPageView from "./AppPageView";

const {ccclass, property} = cc._decorator;

enum ViewType {
    SCROLL,
    Flip
}

enum LayoutType {
    GRID,
    LIST
}


@ccclass
export default class ListView extends cc.Component {

    @property(cc.Prefab)
    itemPrefab: cc.Prefab = null;

    @property
    initCount: number = 0;

    @property(cc.ScrollView)
    scrollView: cc.ScrollView = null;

    @property
    bufferZone: number = 0;

    @property
    itemHeight: number = 0;

    @property({
        type: cc.Enum(ViewType)
    })
    viewType: ViewType = ViewType.SCROLL;

    @property({
        type: cc.Enum(LayoutType)
    })
    layoutType: LayoutType = LayoutType.LIST

    @property
    itemWidth: number = 0;

    @property
    spaceY: number = 0;

    @property
    spaceX: number = 0;

    @property
    column: number = 1;

    @property
    offsetStart:number = 0;


    private _itemList: ListItemDelegate[] = [];
    private updateTimer: number = 0;
    private updateInterval: number = 0.05;
    private lastContentPosY: number = 0;

    dataList: any[] = [];


    get itemList(): ListItemDelegate[] {
        return this._itemList;
    }

    onLoad() {
        this.setup();
    }

    private setup() {
        this.preItems();
    }

    private preItems() {
        // this.initCount =  this.itemPrefab.data.height

        if (this.layoutType == LayoutType.LIST) {
            let y = -this.itemHeight * 0.5;
            for (let i = 0; i < this.initCount; i++) {
                let delegate = cc.instantiate(this.itemPrefab).getComponent(ListItemDelegate);
                this._itemList.push(delegate);
                delegate.updateItem(i, 0, y, null);
                y -= this.itemHeight + this.spaceY;
                this.node.addChild(delegate.node);
                delegate.node.active = false;
            }
        } else {
            for (let i = 0; i < this.initCount; i++) {
                let delegate = cc.instantiate(this.itemPrefab).getComponent(ListItemDelegate);
                this._itemList.push(delegate);
                let row = Math.floor(i / this.column);
                let col = Math.floor(i % this.column) + 1;
                let y = -this.itemHeight / 2 - (row - 1) * this.spaceY - row * this.itemHeight;
                let x = this.itemWidth / 2 + (col - 1) * (this.spaceX + this.itemWidth);
                delegate.updateItem(i, x, y, null);
                delegate.node.active = false;
                this.node.addChild(delegate.node);
            }
        }
        // this.initCount = Math.ceil(this.scrollView.node.height / (this.itemPrefab.data.height + this.spaceY));

    }

    reloadList() {

        if (this.layoutType == LayoutType.LIST) {
            let y = -this.itemHeight * 0.5;
            for (let i = 0; i < this.initCount; i++) {
                // let delegate = cc.instantiate(this.itemPrefab).getComponent(ListItemDelegate);
                // this._itemList.push(delegate);
                let delegate = this.itemList[i];
                // delegate.node.opacity = 5;
                if (i < this.dataList.length) {
                    delegate.node.active = true;
                    delegate.updateItem(i, 0, y, this.dataList[i]);
                } else {
                    delegate.node.active = false;
                }
                y -= this.itemHeight + this.spaceY;
                // this.node.addChild(delegate.node);
            }
            this.scrollView.content.height = (this.dataList.length) * (this.itemHeight + this.spaceY);
        } else {
            for (let i = 0; i < this._itemList.length; i++) {
                let delegate = this._itemList[i];
                let row = Math.floor(i / this.column);
                let col = Math.floor(i % this.column) + 1;
                let y = -this.itemHeight / 2 - (row - 1) * this.spaceY - row * this.itemHeight;
                let x = this.itemWidth / 2 + (col - 1) * (this.spaceX + this.itemWidth);
                if (i < this.dataList.length) {
                    delegate.node.active = true;
                    delegate.updateItem(i, x, y, this.dataList[i]);
                } else {
                    delegate.node.active = false;
                }
            }
            let rowSize = Math.ceil(this.dataList.length / this.column);
            if (this.viewType == ViewType.Flip) {
                let pageRowSize = (this.scrollView.node.height + this.spaceY) / (this.itemHeight + this.spaceY)
                let page = Math.ceil(rowSize / pageRowSize);
                let appPageView = this.scrollView.node.getComponent(AppPageView);
                if (appPageView) {
                    appPageView.pageSize = page;

                }

            } else {
                this.scrollView.content.height = rowSize * (this.itemHeight + this.spaceY) - this.spaceY;
            }
            // this.scrollView.content.height = (this.dataList.length) * (this.itemHeight + this.spaceY);

        }

    }

    update(dt) {
        this.updateTimer += dt;
        if (this.updateTimer < this.updateInterval) {
            return; // we don't need to do the math every frame
        }
        this.updateTimer = 0;
        let items = this._itemList;
        let buffer = this.bufferZone;
        let isDown = this.node.y < this.lastContentPosY; // scrolling direction
        let isUp = this.node.y > this.lastContentPosY;
        let curItemCount = this._itemList.length;
        let offset = 0;
        if (this.layoutType == LayoutType.LIST) {
            offset = (this.itemHeight + this.spaceY) * curItemCount - this.spaceY;
        } else {
            offset = (this.itemHeight + this.spaceY) * Math.floor(curItemCount / this.column);
        }

        // let offset = (this.itemHeight + this.spaceY) * curItemCount;
        for (let i = 0; i < curItemCount; ++i) {
            let item = items[i];
            let itemNode = item.node;
            let viewPos = this.getPositionInView(itemNode);
            if (isDown) {
                // if away from buffer zone and not reaching top of content
                if (viewPos.y + this.offsetStart < -buffer && itemNode.y + offset < 0) {
                    let newIdx = item.index - curItemCount;
                    console.log(newIdx);
                    if (newIdx < this.dataList.length) {
                        let newInfo = this.dataList[newIdx];
                        if (this.layoutType == LayoutType.LIST) {
                            item.updateItem(newIdx, 0, itemNode.y + offset, newInfo);
                        } else {
                            let row = Math.floor(newIdx / this.column);
                            let col = Math.floor(newIdx % this.column) + 1;
                            let y = -this.itemHeight / 2 - (row - 1) * this.spaceY - row * this.itemHeight;
                            let x = this.itemWidth / 2 + (col - 1) * (this.spaceX + this.itemWidth);
                            item.updateItem(newIdx, x, y, newInfo);
                        }
                        // item.updateItem(newIdx, 0, itemNode.y + offset, newInfo);
                    }
                }
            } else if (isUp) {
                // if away from buffer zone and not reaching bottom of content
                if (viewPos.y - this.offsetStart > buffer && itemNode.y - offset > -this.node.height) {
                    let newIdx = item.index + curItemCount;
                    // let newInfo = this.itemList[newIdx];
                    console.log(newIdx);
                    if (newIdx < this.dataList.length) {
                        let newInfo = this.dataList[newIdx];
                        if (this.layoutType == LayoutType.LIST) {
                            item.updateItem(newIdx, 0, itemNode.y - offset, newInfo);
                        } else {
                            let row = Math.floor(newIdx / this.column);
                            let col = Math.floor(newIdx % this.column) + 1;
                            let y = -this.itemHeight / 2 - (row - 1) * this.spaceY - row * this.itemHeight;
                            let x = this.itemWidth / 2 + (col - 1) * (this.spaceX + this.itemWidth);
                            item.updateItem(newIdx, x, y, newInfo);
                        }
                    }
                }
            }
        }
        // update lastContentPosY
        this.lastContentPosY = this.node.y;
    }

    getPositionInView(item) { // get item position in scrollview's node space
        let worldPos = item.parent.convertToWorldSpaceAR(item.position);
        let viewPos = this.scrollView.node.convertToNodeSpaceAR(worldPos);
        return viewPos;
    }
}