//  - [English] http://www.cocos2d-x.org/docs/creator/manual/en/scripting/life-cycle-callbacks.html

import NumberInput from "./NumberInput";
import BuyAlert from "./BuyAlert";
import {hideKeyBoard, playAlertCloseSound, playBtnSound} from "../utils/Helper";

const {ccclass, property} = cc._decorator;

@ccclass
export default class KeyBoard extends cc.Component {

    @property(cc.Node)
    numberPad: cc.Node = null;

    @property(cc.Node)
    mask:cc.Node = null;


    @property(cc.Node)
    selects: cc.Node = null;


    @property(cc.Node)
    btnClose:cc.Node = null;

    // LIFE-CYCLE CALLBACKS:

    target: NumberInput = null;

    // onLoad () {}

    numbers: string[] = ['7', '8', '9', '4', '5', '6', '1', '2', '3', '0']


    start() {
        for (let i = 0; i < this.numberPad.childrenCount; i++) {
            let node = this.numberPad.children[i];
            let button = node.getComponent(cc.Button);
            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node; //这个 node 节点是你的事件处理代码组件所属的节点
            clickEventHandler.component = "KeyBoard";//这个是代码文件名
            clickEventHandler.handler = "numberClick";
            clickEventHandler.customEventData = i + "";
            button.clickEvents.push(clickEventHandler);
        }
        this.selects.children.forEach((item) => {
            let button = item.getComponent(cc.Button);
            let clickEventHandler = new cc.Component.EventHandler();
            clickEventHandler.target = this.node; //这个 node 节点是你的事件处理代码组件所属的节点
            clickEventHandler.component = "KeyBoard";//这个是代码文件名
            clickEventHandler.handler = "selectClick";
            let label = cc.find("number", item).getComponent(cc.Label);
            clickEventHandler.customEventData = label.string;
            button.clickEvents.push(clickEventHandler);
        })
        this.btnClose.on("click",()=>{
            playAlertCloseSound();
            this.closeKeyBoard();
        })
        this.mask.on("click",()=>{
            playAlertCloseSound();
            this.closeKeyBoard();
        })
    }

    selectClick(event, customEventData: string) {
        this.target.number = customEventData;
        this.closeKeyBoard();
        playBtnSound();
    }

    numberClick(event, customEventData: string) {
        console.log(customEventData);
        let number = parseInt(customEventData);
        if (this.numbers.length > number) {
            let input = this.numbers[number];
            this.target.input(input);
        } else {
            if (number == 10) {
                this.clearClick();
            } else if (number == 11) {
                this.deleteString();
            }
        }
        playBtnSound();
    }

    deleteString() {
        // let number = this.target.number;
        this.target.delete();
    }

    clearClick() {
        this.target.number = "";
    }


    // update (dt) {}
    private closeKeyBoard() {
        hideKeyBoard();
        BuyAlert.resumeCloseAction();
    }
}
