import HelpAlert from "../alert/HelpAlert";
import AppData from "../AppData";

export function startWith(string: string, start: string) {
    return string.indexOf(start) != -1;
}

export function isRemoteRes(url: string) {
    return startWith(url, "http://") || startWith(url, "https://");
}

export function loadSpriteFrame(url: string) {
    return new Promise((resolve, reject) => {
        let spriteFrame = AppData.imagesCaches.get(url);
        if (spriteFrame) {
            resolve({img: spriteFrame, url: url});
        } else {
            if (isRemoteRes(url)) {
                if (cc.sys.isNative) {
                    cc.loader.load(url, function (error, texture) {
                        if (error) {
                            console.log(error);
                            reject(error);
                        } else {
                            let spriteFrame = new cc.SpriteFrame(texture);
                            resolve({img:spriteFrame,url:url});
                            AppData.imagesCaches.set(url, spriteFrame);
                        }
                    });
                } else {
                    url = url.replace(appConfig.IMG_URL, "/imgs");
                    let img = new Image();
                    img.src = url;
                    img.crossOrigin = "anonymous";
                    img.onload = () => {
                        let texture = new cc.Texture2D();
                        texture.initWithElement(img);
                        texture.handleLoadedTexture();
                        let sf = new cc.SpriteFrame(texture);
                        // cc.log(3, sf);
                        AppData.imagesCaches.set(url, sf);
                        resolve({img: sf, url: url});
                    }
                }

            } else {
                cc.loader.loadRes(url, cc.SpriteFrame, function (error, texture) {
                    if (error) {
                        console.log(error);
                        reject(error);
                    } else {
                        // let spriteFrame = new cc.SpriteFrame(texture);
                        resolve({img: texture, url: url});
                        AppData.imagesCaches.set(url, texture);
                    }
                })
            }
        }
    });
}

let displayImage = {
    target: null,
    load(url) {
        loadSpriteFrame(url).then((res: { img: cc.SpriteFrame, url: string }) => {
            if (res.url = this.target.url) {
                this.target.sprite.spriteFrame = res.img;
            }
        });
        return this;
    },
    to(target: { url: string, sprite: cc.Sprite }) {
        this.target = target;
    },
};

export {displayImage}


export function showKeyBoard() {
    AppGame.numberKeyBoard.opacity = 0;
    AppGame.numberKeyBoard.setScale(0.5, 0.5);
    AppGame.numberKeyBoard.runAction(cc.spawn(cc.fadeIn(0.3), cc.scaleTo(0.3, 1).easing(cc.easeBackOut())));

}


export function hideKeyBoard() {
    AppGame.numberKeyBoard.opacity = 255;
    AppGame.numberKeyBoard.setScale(1, 1);
    let finished = cc.callFunc(() => {
        AppGame.numberKeyBoard.removeFromParent();
    }, this);
    AppGame.numberKeyBoard.runAction(cc.sequence(cc.spawn(cc.fadeOut(0.3), cc.scaleTo(0.3, 0.5).easing(cc.easeBackIn())), finished));
}

function setupGames(gameRes: any) {
    if (gameRes.code == 0) {
        let data = gameRes.data;
        AppGame.gamesData = data;
    }
}

export function pagination(pageNo, pageSize, array) {
    let offset = (pageNo - 1) * pageSize;
    return (offset + pageSize >= array.length) ? array.slice(offset, array.length) : array.slice(offset, offset + pageSize);
}

