export function showLoading() {
    if (AppGame.pageViewLoading) {
        cc.find("Canvas").addChild(AppGame.pageViewLoading);
    } else {
        cc.loader.loadRes("loading/mask", cc.Prefab, (error, prefab: cc.Prefab) => {
            if (error) {
                cc.error(error);
            } else {
                let mask = cc.instantiate(prefab);
                AppGame.pageViewLoading = mask;
                AppGame.pageViewLoading.parent = cc.find("Canvas");
            }
        })
    }

}

