import ApiNet from "../utils/ApiNet";
import LogItemDelegate from "./LogItemDelegate";
import ListView from "../widget/ListView";
import {playAlertCloseSound, playAlertOpenSound} from "../utils/Helper";

const {ccclass, property} = cc._decorator;

@ccclass
export default class LogListAlert {

    private static _alert: cc.Node = null;

    private static _mask: cc.Node = null;

    private static _titleLabel: cc.Label = null;

    private static _cancelButton: cc.Node = null;

    private static _enterButton: cc.Node = null;

    private static _speed: number = 0.3;

    private static actionFadeIn: cc.ActionInterval = null;

    private static _enterAction: Function = null;

    private static actionFadeOut: cc.ActionInterval = null;

    private static _bg: cc.Node = null;

    private static page: number = 1;
    private static _content: cc.Node = null;

    private static logNodePool: cc.NodePool = new cc.NodePool();

    static prefab: cc.Prefab = null;
    private static scrollView: cc.Node = null;
    private static hasMore: boolean = true;
    private static _listView: ListView = null;
    private static _pageLoading: cc.Node;


    static preItems() {

    }

    static show(speed: number) {
        this._speed = speed ? speed : this._speed;
        if (this._alert != null) {
            this._alert.setLocalZOrder(999);
            this.startFadeIn();
            return;
        }
        this._speed = speed ? speed : this._speed;

        cc.loader.loadRes("prefabs/LogListAlert", cc.Prefab, (error, prefab) => {
            if (error) {
                cc.error(error);
                return;
            }
            this._alert = cc.instantiate(prefab);
            this._mask = cc.find("mask", this._alert);
            this._bg = cc.find("alertBg", this._alert);

            let cbFadeOut = cc.callFunc(this.onFadeOutFinish, this);
            let cbFadeIn = cc.callFunc(this.onFadeInFinish, this);
            this.actionFadeIn = cc.sequence(cc.moveBy(this._speed, cc.p(-this._bg.width)), cbFadeIn);
            this.actionFadeOut = cc.sequence(cc.moveBy(this._speed, cc.p(this._bg.width)), cbFadeOut);
            // 获取子节点
            this.scrollView = cc.find("alertBg/ScrollView", this._alert);
            this._content = cc.find("alertBg/ScrollView/view/list", this._alert);
            this._listView = this._content.getComponent(ListView);
            this._pageLoading = cc.find("alertBg/loading",this._alert);
            console.log('--------初始化listView');
            // this._listView.setup();
            // window['alert']['listView'] = this._listView;
            this._cancelButton = cc.find("alertBg/btnCancel", this._alert);
            this._cancelButton.on('click', this.onButtonClicked, this);
            this._alert.parent = cc.find("Canvas");

            this.scrollView.on("LoadMore", () => {
                this.page++;
                this.loadLogs();
            })
            this.startFadeIn();

        });
    }

    // 按钮点击事件
    static onButtonClicked(event) {
        if (event.target.name == "enterButton") {
            if (this._enterAction) {
                this._enterAction();
            }
        }
        this.startFadeOut();
    };

    static startFadeOut() {
        playAlertCloseSound();
        cc.eventManager.pauseTarget(this._alert, true);
        this._mask.runAction(cc.fadeOut(this._speed));
        this._alert.runAction(this.actionFadeOut);
    };


    static loadLogs() {
        if (this.hasMore) {
            ApiNet.post("/user/my/bet", {page: this.page}).then((res) => {
                console.log(res);
                if (res.code == 0) {
                    LogListAlert.setupLogData(res.data);
                }
            });
        } else {
            cc.find("alertBg/ScrollView/loading", this._alert).runAction(cc.fadeOut(0.15));
            this._pageLoading.runAction(cc.fadeOut(0.15));
        }

    }

    private static setupLogData(data: any[]) {
        if (data.length < 10) {
            this.hasMore = false;
            if (this.page > 1) {
                this.setupFooter();
            }
        }

        if (this.page == 1) {
            this._listView.dataList = data;
            this._pageLoading.runAction(cc.fadeOut(0.15));
        } else {
            this._listView.dataList = this._listView.dataList.concat(data);
        }
        this._listView.reloadList();

        // for (let i = 0; i < data.length; i++) {
        //     let item = cc.instantiate(this.prefab);
        //     item.parent = this._content;
        //     item.getComponent(LogItemDelegate).item = data[i];
        // }
        if (this.page == 1) {
            this.scrollView.getComponent(cc.ScrollView).scrollToTop(0);
        }
        cc.find("alertBg/ScrollView/loading", this._alert).runAction(cc.fadeOut(0.15));
    }


    private static setupFooter() {
        cc.loader.loadRes("prefabs/footer", cc.Prefab, (error, resource) => {
            if (error) {
                cc.error(error);
                return;
            }
            let footer = cc.instantiate(resource);
            footer.name = "loadMoreFooter";
            footer.parent = this._content;
            footer.y = -this._content.height - footer.height * 0.5
        })
    }

// 弹进动画完成回调
    static onFadeInFinish() {
        this._alert.resumeSystemEvents(true);
        this.page = 1;
        this.loadLogs();
    }

    // 弹出动画完成回调
    static onFadeOutFinish() {
        // this.onDestory();
    };


    static onDestory() {
        this._alert.destroy();
        this._enterAction = null;
        this._alert = null;
        this._titleLabel = null;
        this._cancelButton = null;
        this._enterButton = null;
        this._speed = 0.3;
    }


    static startFadeIn() {
        playAlertOpenSound();
        this.hasMore = true;
        this._alert.pauseSystemEvents(true);
        this._alert.position = cc.p(0, 0);
        this._mask.opacity = 0;
        this._mask.runAction(cc.fadeTo(this._speed, 156));
        this._alert.runAction(this.actionFadeIn);
    }


}

