import ListItemDelegate from "../widget/ListItemDelegate";
import AppGame from "../AppGame";
import {playSelectSound} from "../utils/Helper";

const {ccclass, property} = cc._decorator;

@ccclass
export default class LogItemDelegate extends ListItemDelegate {

    @property(cc.Label)
    timeLabel: cc.Label = null;
    @property(cc.Label)
    infoLabel: cc.Label = null;
    @property(cc.Label)
    moneyLabel: cc.Label = null;
    @property(cc.Label)
    resultLabel: cc.Label = null;

    private _item: any = null;


    get item(): any {
        return this._item;
    }

    set item(value: any) {
        this._item = value;
        this.updateData();
    }


    updateItem(index: number, x: number, y: number, model: any): void {
        super.updateItem(index, x, y, model);
        if (model) {
            this.item = model;
        }
    }

    onItemClick() {

    }

    private updateData() {
        let dayjs = window['dayjs'];
        if (this.item) {
            let date = dayjs(this.item.create_time * 1000);
            this.timeLabel.string = date.format("MM月DD日\nHH:mm");
            // this.timeLabel.string = date.format("MM-DD") + "\n" + date.format("HH:mm");
            this.infoLabel.string = this.item.teams[0].name + "VS" + this.item.teams[1].name + "\n"
                + this.item.bet_target.rules_name + " " + "\n"
                + "赔率:" + this.item.odds;
            this.moneyLabel.string = this.item.money + "\n" + this.item.bet_target.target;
            let status = parseInt(this.item.play.status);
            if (status != 100) {
                let money = parseFloat(this.item.odds) * parseFloat(this.item.money);
                this.resultLabel.string = "等待结算" + "\n" + "可赢" + money.toFixed(2);
                this.resultLabel.node.color = cc.hexToColor("#f0a51e")
            } else {
                let win_money = this.item.win_money;
                if (win_money > 0) {
                    this.resultLabel.string = "胜" + "+" + win_money;
                    this.resultLabel.node.color = cc.hexToColor("#f0380c")
                } else {
                    this.resultLabel.string = "负" + "-" + parseFloat(this.item.money).toFixed(2);
                    this.resultLabel.node.color = cc.hexToColor("#45f02b")
                }
            }
            // this.timeLabel.string =
            // let time = new Date(this.item.play_time * 1000);
            // this.timeLabel.string = time.toLocaleDateString();
            // this.infoLabel.string = this.item.match.name + "\n" + this.item.teams[0].name + "VS" + this.item.teams[1].name;

        }
    }

    // update (dt) {}
}
