import AppData from "../AppData";
import nodeCaches from "../NodeCaches";
import AppGame from "../AppGame";
import BuyItemDelegate from "./BuyItemDelegate";
import BuyAlert from "./BuyAlert";
import {playAlertOpenSound, showKeyBoard} from "../utils/Helper";
import KeyBoard from "./KeyBoard";

const {ccclass, property} = cc._decorator;

@ccclass
export default class NumberInput extends cc.Component {

    @property(cc.Label)
    inputNumber: cc.Label = null;

    @property(cc.Label)
    placeHolder: cc.Label = null;

    max: number = 10000;
    min: number = 100;

    private _number: string = "";

    get number(): string {
        return this._number;
    }

    @property
    set number(value: string) {
        this._number = value;
        this.updateFrame();
    }

    input(key: string) {
        if ((this.number == "" && key == ".") || (this.number == "" && key == "00") || (this.number == "0" && key == "0") || (this.number == "0" && key == "00")) {
            return;
        }
        if (this.number.indexOf('.') != -1 && key == ".") {
            return;
        }
        if (this.number.indexOf('.') != -1) {
            let index = this.number.indexOf('.');
            if (this.number.length > index + 2) {
                return;
            }
        }
        this.number = this.number + key;
        if (parseInt(this.number) > 10000) {
            this.number = "10000";
        }
        // this.scheduleOnce(() => {
        //     if (parseInt(this.number) < 100) {
        //         this.number = "";
        //     }
        // }, 0.5);
    }

    delete() {
        this.number = this.number.substr(0, this.number.length - 1);
    }


    private updateFrame() {
        this.inputNumber.string = this._number;
        if (this._number != "") {
            this.placeHolder.node.active = false;
            this.inputNumber.node.active = true;
        } else {
            this.placeHolder.node.active = true;
            this.inputNumber.node.active = false;
        }
        this.node.emit("onNumberChange", this.number);
    }

    onOpenKeyBoard() {
        // BuyAlert._mask

        let component = this.node.parent.parent.getComponent(BuyItemDelegate);
        if (!AppGame.numberKeyBoard) {
            let keyBord = nodeCaches.prefabs.get("keyboard");
            if (keyBord) {
                AppGame.numberKeyBoard = cc.instantiate(keyBord);
            }
        }
        let x = 0;
        let y = 180;
        console.log(component.node.x);
        if (component.node.parent.childrenCount - 1 == component.index) {
            x = component.node.x - 230 - 339;
        } else {
            x = component.node.x + 230 + 339;
        }
        cc.find("content", AppGame.numberKeyBoard).getComponent(KeyBoard).target = this;
        // AppGame.numberKeyBoard.getComponent(KeyBoard).target = this;
        AppGame.numberKeyBoard.x = x;
        AppGame.numberKeyBoard.y = y;
        AppGame.numberKeyBoard.parent = BuyAlert._keyboardContainer;
        showKeyBoard();
        playAlertOpenSound();
    }

// LIFE-CYCLE CALLBACKS:

    // onLoad () {}

    start() {

    }

    // update (dt) {}
}
